import React, { Component } from 'react'
import './App.css';
import ListItem from './ListItem'

export class App extends Component {
  constructor(props){
   super(props)
   this.state = {
     items:[],
     currentItem:{
       text:'',
       key:''
     }
   }
   this.handleChange=this.handleChange.bind(this)
   this.addHandle=this.addHandle.bind(this)
   this.deleteItems=this.deleteItems.bind(this)
   this.setUpdate=this.setUpdate.bind(this)

  }
  handleChange(e){
    this.setState({
      currentItem:{
        text:e.target.value,
        key:Date.now()
      }
    })
  }
 addHandle(e){
   e.preventDefault();
   const newitem=this.state.currentItem
   console.log(newitem)
   if(newitem.text!==''){
     const newItems=[...this.state.items,newitem];
     this.setState({
       items:newItems,
       currentItem:{
         text:'',
         key:''
       }
     })
   }
 }
 deleteItems(key){
   const filtered = this.state.items.filter(item =>
   item.key!==key
   );
   this.setState({
     items:filtered
   })
 }
 setUpdate(text,key){
   const items=this.state.items;
    items.map(item =>{
     if(item.key===key){
       item.text=text;
     }
   })
   this.setState({
     items:items
   })
 }
  render() {
    return (
      <div className="App">
       <header>
        <form id="to-do" onSubmit={this.addHandle}>
         <input type='text' value={this.state.currentItem.text} placeholder="entertext" onChange={this.handleChange} />
         <button type='submit'>Add</button>
        </form>
       </header>
       <ListItem items={this.state.items} deleteItems={this.deleteItems} setUpdate={this.setUpdate} />
      </div>
    )
  }
}

export default App
