import React from 'react'
//import { Icon } from '@material-ui/core';
import FlipMove from 'react-flip-move'
import './Listitems.css'

import DeleteIcon from '@material-ui/icons/Delete';

function ListItem(props) {
    const items=props.items;
    const listIteam=items.map(item =>
    {
        return <div className='list' key={item.key}>
           <p><input type='text' id={item.key} value={item.text} onChange={(e) => props.setUpdate(e.target.value,item.key)} />
           <span> <DeleteIcon  className='faicons'
           onClick={()=>props.deleteItems(item.key)} /></span></p>
        </div>
    })
    return (
        <div>
        <FlipMove duration={500} easing='ease-in-out'>
            {listIteam } 
         </FlipMove>   
        </div>
    )
}

export default ListItem
